import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Permissao_acesso } from '../../../modelo/Permissao_acesso'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'permissao_acesso-dados-details',   templateUrl: 'permissao_acesso_dadosDetails.component.html' }) export class Permissao_acessoDadosDetailsComponent implements OnInit {    permissao_acesso: Permissao_acesso;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.permissao_acesso.Permissao_acesso";                 let id = +params['id'];         this.permissao_acesso = new Permissao_acesso();         this.permissao_acesso.cd_permissao_acesso = id;         o.objetoJson = this.permissao_acesso;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a permissao_acesso. '; }      this.permissao_acesso = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


